import { connect } from 'react-redux';
import WalletCardComponent from './WalletCardComponent';
import { getProductByIdSelector } from '../../redux/selectors/productPositionSelectors';
import { selectPositionAndSave } from '../../redux/actions/positionActions';

const mapStateToProps = (state, ownProps) => {
  const {
    iconFileName,
    ProductSymbol,
    ProductFullName,
    Amount,
    ProductType,
    ProductId
  } = getProductByIdSelector(state, ownProps.productId);
  const isCrypto = ProductType === 'CryptoCurrency';

  return {
    iconFileName,
    ProductSymbol,
    ProductFullName,
    Amount,
    isCrypto,
    ProductId
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    selectPositionAndSave: () => {
      dispatch(selectPositionAndSave(ownProps.productId));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(WalletCardComponent);



