import React from 'react';
import PropTypes from 'prop-types';
import APIconButton from '../common/APIconButton/APIconButton';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import './WalletNavItems.css';

const fiatNavItemsClasses = getBEMClasses('wallet-nav-item');

class FiatNavItems extends React.Component {
  onDepositClick = () => {
    this.onBtnClick(true);
  };
  onWithdrawClick = () => {
    this.onBtnClick(false);
  };
  onBtnClick = isSend => {
    const { openRetailFiatSidePane, product } = this.props;

    openRetailFiatSidePane(isSend, product);
  };

  render() {
    return (
      <React.Fragment>
        <APIconButton
          iconName={'deposit'}
          action={this.onDepositClick}
          tooltipText={this.context.t('Deposit')}
          customClass={fiatNavItemsClasses()}
        />
        <APIconButton
          iconName={'withdraw'}
          action={this.onWithdrawClick}
          tooltipText={this.context.t('Withdraw')}
          customClass={fiatNavItemsClasses()}
        />
      </React.Fragment>
    );
  }
}

FiatNavItems.propTypes = {
  openRetailFiatSidePane: PropTypes.func.isRequired,
  product: PropTypes.shape({
    iconFileName: PropTypes.string,
    ProductFullName: PropTypes.string,
    ProductSymbol: PropTypes.string,
    ProductId: PropTypes.number
  })
};

FiatNavItems.contextTypes = {
  t: PropTypes.func.isRequired
};

export default FiatNavItems;



