import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import path from '../../helpers/path';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import ProductIconContainer from '../common/ProductIcon/ProductIconContainer';
import APIcon from '../common/APIcon/APIcon';
import CryptoNavItemsContainer from '../WalletNavItems/CryptoNavItemsContainer';
import FiatNavItemsContainer from '../WalletNavItems/FiatNavItemsContainer';
import { walletDetailPath } from '../../routeTemplates';

import './WalletCard.css';

const walletCardClasses = getBEMClasses('wallet-card');

const WalletCard = (props, context) => {
  const {
    iconFileName,
    ProductSymbol,
    ProductFullName,
    Amount,
    isCrypto,
    ProductId
  } = props;

  return (
    <div className={`${walletCardClasses()} container-with-shadow`}>
      <div className={walletCardClasses('icon-label-row')}>
        <div className={walletCardClasses('icon')}>
          <ProductIconContainer iconFileName={iconFileName} size={44} />
        </div>
        <span className={walletCardClasses('symbol-label')}>
          {`${ProductSymbol} ${ProductFullName}`}
        </span>
      </div>
      <div className={walletCardClasses('balance-row')}>
        <div className={walletCardClasses('balance')}>
          <span className={walletCardClasses('label')}>
            {context.t('Current Balance')}
          </span>
          <div className={walletCardClasses('amount')}>{Amount}</div>
        </div>
      </div>
      <div className={walletCardClasses('activity-link-row')}>
        <div className={walletCardClasses('activity-link-container')}>
          <Link
            className={walletCardClasses('activity-link')}
            to={path(walletDetailPath.path)}
            onClick={props.selectPositionAndSave}>
            {context.t('View Activity')}{' '}
            <APIcon
              name="arrowNext"
              customClass={walletCardClasses('next-icon')}
            />
          </Link>
        </div>
        <div className={walletCardClasses('icon-nav-buttons')}>
          {isCrypto ? (
            <CryptoNavItemsContainer
              product={{
                iconFileName,
                ProductSymbol,
                ProductFullName,
                ProductId
              }}
            />
          ) : (
            <FiatNavItemsContainer
              product={{
                iconFileName,
                ProductSymbol,
                ProductFullName,
                ProductId
              }}
            />
          )}
        </div>
      </div>
    </div>
  );
};

WalletCard.contextTypes = {
  t: PropTypes.func.isRequired
};

export default WalletCard;



