import React from 'react';
import PropTypes from 'prop-types';
import APIconButton from '../common/APIconButton/APIconButton';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { product } from '../../propTypes/sendReceiveComponent';
import './WalletNavItems.css';

const cryptoNavItemsClasses = getBEMClasses('wallet-nav-item');

class CryptoNavItems extends React.Component {
  onSendClick = () => {
    this.onBtnClick(true);
  };
  onReceiveClick = () => {
    this.onBtnClick(false);
  };
  onBtnClick = isSend => {
    const { openSendReceiveSidePane, product } = this.props;

    openSendReceiveSidePane(isSend, product);
  };
  render() {
    return (
      <React.Fragment>
        <APIconButton
          iconName={'send'}
          action={this.onSendClick}
          tooltipText={this.context.t('Send')}
          customClass={cryptoNavItemsClasses()}
        />
        <APIconButton
          iconName={'receive'}
          action={this.onReceiveClick}
          tooltipText={this.context.t('Receive')}
          customClass={cryptoNavItemsClasses()}
        />
      </React.Fragment>
    );
  }
}

CryptoNavItems.propTypes = {
  openSendReceiveSidePane: PropTypes.func.isRequired,
  product
};

CryptoNavItems.contextTypes = {
  t: PropTypes.func.isRequired
};

export default CryptoNavItems;



