import { connect } from 'react-redux';
import { openRetailFiatSidePane } from '../../redux/actions/sidePaneActions';
import FiatNavItemsComponent from './FiatNavItemsComponent';

const mapDispatchToProps = dispatch => ({
  openRetailFiatSidePane: (isSend, product) => {
    dispatch(openRetailFiatSidePane(isSend, product));
  }
});

export default connect(
  null,
  mapDispatchToProps
)(FiatNavItemsComponent);



